<template>
  <v-app id="btruck">
    <Navbar />
    <v-layout class="my-5" row wrap>
      <v-flex xs1 md3 sm2 lg4></v-flex>
      <v-flex xs10 sm8 md6 lg4>
        <!--Truck booking form begining-->
        <validation-observer ref="observer" v-slot="{ invalid }">
          <form id="book" @submit.prevent="submit">
            <v-layout class="my-2" row wrap
              ><v-flex class="mx-3"><h2>Book a Truck</h2></v-flex
              ><v-flex></v-flex
            ></v-layout>
            <validation-provider
              v-slot="{ errors }"
              name="Startlocation"
              rules="required|max:20"
            >
              <v-autocomplete
                v-model="startlocation"
                :error-messages="errors"
                :items="searchResults"
                label="Start Location"
                prepend-inner-icon="mdi-map-marker"
                outlined
                rounded
                dense
              ></v-autocomplete>
            </validation-provider>
            <!-- <vue-google-autocomplete
              id="map"
              classname="form-control"
              placeholder="Start typing"
            >
            </vue-google-autocomplete> -->

            <validation-provider
              v-slot="{ errors }"
              name="Endlocation"
              rules="required|max:20"
            >
              <v-text-field
                id="endloc"
                v-model="endlocation"
                :error-messages="errors"
                label="End Location"
                prepend-inner-icon="mdi-map-marker"
                outlined
                rounded
                dense
              ></v-text-field>
            </validation-provider>
            <validation-provider
              v-slot="{ errors }"
              name="Date"
              rules="required"
            >
              <v-text-field
                v-model="date"
                :error-messages="errors"
                label="Date of Transport"
                type="date"
                outlined
                rounded
                dense
              ></v-text-field>
            </validation-provider>

            <validation-provider
              v-slot="{ errors }"
              name="Weight"
              :rules="{
                required: true,
              }"
            >
              <v-text-field
                v-model="weight"
                :error-messages="errors"
                label="Weight in ton"
                outlined
                rounded
                dense
              ></v-text-field>
            </validation-provider>
            <validation-provider
              v-slot="{ errors }"
              name="Goods Type"
              rules="required|max:10"
            >
              <v-text-field
                v-model="goodstype"
                :error-messages="errors"
                label="Goods Type"
                outlined
                rounded
                dense
              ></v-text-field>
            </validation-provider>
            <v-layout class="my-1" row wrap>
              <v-flex lg2></v-flex>
              <v-flex>
                <v-btn
                  dark
                  block
                  type="submit"
                  :disabled="invalid"
                  router
                  @click.prevent="saveData"
                >
                  Continue
                </v-btn>
              </v-flex>
              <v-flex lg2></v-flex>
            </v-layout>
          </form>
        </validation-observer>
        <!--Truck booking form begining-->
      </v-flex>
    </v-layout>
  </v-app>
</template>
<script>
import Navbar from "../components/Navbar";
import { required, digits, max } from "vee-validate/dist/rules";
import {
  extend,
  ValidationObserver,
  ValidationProvider,
  setInteractionMode,
} from "vee-validate";

setInteractionMode("eager");

//Custom validation form input fields

extend("digits", {
  ...digits,
  message: "{_field_} needs to be digits. ({_value_})",
});

extend("required", {
  ...required,
  message: "required",
});

extend("max", {
  ...max,
  message: "{_field_} may not be greater than {length} characters",
});
//Custom validation ends
export default {
  name: "Booking",
  components: {
    ValidationProvider,
    ValidationObserver,
    Navbar,
  },
  data: () => {
    return {
      checkbox: "",
      name: "",
      startlocation: "",
      searchResults: [],
      endlocation: "",
      service: null,
      weight: "",
      goodstype: "",
      date: "",
      autocomplete: "",
    };
  },
  watch: {
    location(newValue) {
      if (newValue) {
        this.service.getPlacePredictions(
          {
            input: this.location,
            types: ["(cities)"],
          },
          this.displaySuggestions
        );
      }
    },
  },
  metaInfo() {
    return {
      script: [
        {
          src: `https://maps.googleapis.com/maps/api/js?key=AIzaSyCKi1SMNFph6FmFScvBfMwFTIwxes7UsHg&libraries=places`,
          async: true,
          defer: true,
          callback: () => this.MapsInit(), // will declare it in methods
        },
      ],
    };
  },
  methods: {
    MapsInit() {
      this.service = new window.google.maps.places.AutocompleteService();
    },
    displaySuggestions(predictions, status) {
      if (status !== window.google.maps.places.PlacesServiceStatus.OK) {
        this.searchResults = [];
        return;
      }
      this.searchResults = predictions.map(
        (prediction) => prediction.description
      );
    },
    submit() {
      this.$refs.observer.validate();
    },
    clear() {
      this.name = "";
      this.startlocation = "";
      this.endlocation = "";
      this.weight = "";
      this.goodstype = "";
      this.$refs.observer.reset();
    },
    saveData() {
      localStorage.setItem("sl", this.startlocation);
      localStorage.setItem("el", this.endlocation);
      localStorage.setItem("dt", this.date);
      localStorage.setItem("wt", this.weight);
      localStorage.setItem("gt", this.goodstype);
      this.clear();
      this.$router.push({ name: "Csignup" });
    },
    //Function to set input field empty
  },
};
</script>
<style scoped>
#book {
  border: solid black 2px;
  padding: 30px;
  border-radius: 30px;
  background-color: white;
}
#btruck {
  background: url("../assets/truck-12.jpg");
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
  margin: auto;
  padding: 0;
}
</style>